import Header from "@/components/common/Header";
import { getDetail, getAddressDefault } from "@/servers/api";
export default {
  name: "",
  props: [""],
  data() {
    return {
      TitleObjData: {
        titleContent: "确认订单",
        showLeftBack: true,
        showRightMore: false
      },
      addressDetails: {},
      goodsDetails: {}
    };
  },

  components: {
    Header
  },

  computed: {
    scenceGiftsContent() {
      return { height: document.documentElement.clientHeight - 45 + "px" };
    }
  },

  beforeMount() {},

  mounted() {
    this.getGoodsDetailsInfo();
    this.getAddressDefault();
  },

  methods: {
    selAddress() {
      this.$router.push("/address");
    },
    // 获取商品详情
    getGoodsDetailsInfo() {
      getDetail({
        goods_id: this.$route.query.id
      })
        .then(res => {
          console.log(res);
          if (res.result === 1) {
            this.goodsDetails = res.data;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 获取默认收货地址
    getAddressDefault() {
      getAddressDefault({})
        .then(res => {
          if (res.result === 1) {
            this.addressDetails = res.data[0];
          } else {
            this.$vux.confirm.show({
              title: "提示",
              content: "请先添加收货地址",
              showCancelButton: false,
              onConfirm: () => {
                this.selAddress();
              }
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },

  watch: {}
};



